
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import md5 from 'vux/src/tools/md5/index.js'

import {checkMobile, checkPassword, is_weixin} from "../../utils/assist/regExp";
import {fly} from "../../common/js/http";

export default {
name: "loign",
components: {
Box,
Group,
XInput,
XButton
},
data() {
return {
userName: '',
password: '',
}
},
async beforeRouteEnter (to, from, next) {
console.log(to)
console.log(from)
console.log(is_weixin())
var cc = await fly.post('/send/smsCodeNew', {scene: 'regist', sendSmsPhone: '123123213312'})
console.log(cc)
next(vm => {
console.log(vm.userName)
})
},
methods: {
login() {
if (!checkMobile(this.userName) || !checkPassword(this.password)) {
return
}
var pwd = md5(this.password + 'TuD00Iqz4ge7gzIe2rmjSAFFKtaIBmnr8S')
this.$http.post('/user/doLogin', {
chnlcd: '',
userName: this.userName,
password: pwd,
imgCode: ''
}).then(
res => {
console.log(res)




this.$http.get('/user/userCenter').then(res => {
console.log(res)
})
}
)
}
}
}



